import React, { Component } from "react";
import "./Uploader.css";
import 'semantic-ui-css/semantic.min.css';

// Import React FilePond
import { FilePond, registerPlugin } from "./react-filepond";

// Import FilePond styles
import "filepond/dist/filepond.min.css";

// Import the Image EXIF Orientation and Image Preview plugins
// Note: These need to be installed separately
// `npm i filepond-plugin-image-preview filepond-plugin-image-exif-orientation --save`
import FilePondPluginImageExifOrientation from "filepond-plugin-image-exif-orientation";
import FilePondPluginImagePreview from "filepond-plugin-image-preview";
import "filepond-plugin-image-preview/dist/filepond-plugin-image-preview.css";
import point1 from "../../resources/img/p1.png";
import point2 from "../../resources/img/p2.png";

// Register the plugins
registerPlugin(FilePondPluginImageExifOrientation, FilePondPluginImagePreview);

// Component
class Uploader extends Component {

  constructor(props) {
    super(props);

    this.state = {
      // Set initial files, type 'local' means this is a file
      // that has already been uploaded to the server (see docs)
      files: [
        {
          source: "Case1.jpg",
          options: {
            type: "local",
          },
        },
      ],
    };
  }

  handleInit() {
    console.log("FilePond instance has initialised", this.pond);
  }

  render() {
    return (
      <div class="segment" align="center">
      <div class="grid" align="left" style={{width: 800}}>
      <div className="Uploader">
    <h3> <img src={point2} height={50} width={50} /> Upload case videos or photos</h3>
              <p>
                Sed ut perspiciatis unde omnis iste natus error sit
                voluptatem accusantium doloremque laudantium, totam rem
                aperiam, eaque ipsa quae ab illo inventore veritatis et
                quasi architecto beatae vitae dicta sunt explicabo.  
              </p>
        <FilePond
          ref={(ref) => (this.pond = ref)}
          files={this.state.files}
          allowMultiple={true}
          server={{
            // fake server to simulate loading a 'local' server file and processing a file
            process: (fieldName, file, metadata, load) => {
              // simulates uploading a file
              setTimeout(() => {
                load(Date.now());
              }, 1500);
            },
            load: (source, load) => {
              // simulates loading a file from the server
              fetch(source)
                .then((res) => res.blob())
                .then(load);
            },
          }}
          oninit={() => this.handleInit()}
          onupdatefiles={(fileItems) => {
            // Set currently active file objects to this.state
            this.setState({
              files: fileItems.map((fileItem) => fileItem.file),
            });
          }}
        ></FilePond>
              <div>scroll down </div>
      </div>
      </div>
      </div>

    );
  }
}

export default Uploader;
